@import "../mixins";
@import "../vars";

.single {
  padding-top: 139rem;

  .container {
    display: flex;
  }

  .aside {
    margin-top: 128rem;
  }
}

.single-page {
  .humburger span {
    background-color: $text_color_light;
  }

  .humburger span::before {
    background-color: $text_color_dark;
  }

  .humburger span::after {
    background-color: $text_color_dark;
  }
}

.single_block {
  margin-left: 126rem;
  display: flex;
  width: 727rem;
  justify-content: center;
  flex-direction: column;

  .quotes {
    margin: 35rem 67rem 25rem;

    .quotes_job {
      font-size: 10rem;
    }

    .quotes_text {
      width: 400rem;
      font-size: 16rem;
    }
  }
}

.single_title {
  color: $title_color;
  font-family: "GTWalsheimPro-Medium", sans-serif;
  font-size: 48rem;
  margin-top: 20rem;
  margin-bottom: 45rem;
  line-height: 50rem;
}

.single_pic {
  display: flex;
  flex-direction: column;

  img {
    width: 100%;
    margin-bottom: 20rem;
  }
}

.single_pic_title {
  color: #9fa3a7;
  font-size: 10rem;
  font-family: "SpaceGrotesk-Regular", sans-serif;
}

.single_text {
  color: $text_color_dark;
  font-family: "SpaceGrotesk-Regular", sans-serif;
  font-size: 16rem;
  line-height: 165%;
  margin: 25rem 67rem;
  width: 593rem;
}

.single_pretitle {
  color: $title_color;
  font-family: "GTWalsheimPro-Medium", sans-serif;
  font-size: 24rem;
  margin: 35rem 67rem 10rem;
}

.article_info.single {
  margin: 0 67rem;
  padding-top: 35rem;

  .author_name {
    margin: auto 0 auto auto;
    font-size: 16rem;
    line-height: 160%;
    font-family: "SpaceGrotesk-Regular", sans-serif;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #9fa3a7;
  }
}

.single_bottom {
  margin-top: 119rem;

  .container {
    padding: 0 126rem 0 124rem;
  }
}

.single_bottom_title {
  margin-top: 0;
  margin-bottom: 60rem;
  color: $title_color;
  font-family: "GTWalsheimPro-Medium", sans-serif;
  font-size: 48rem;
}

.topic_list {
  display: flex;
  justify-content: space-between;
}

.topic_item {
  display: flex;
  width: 190rem;
  flex-direction: column;
}

.topic_item_pic {
  width: 100%;
  margin-bottom: 20rem;
}

.topic_item_link {
  font-family: "GTWalsheimPro-Medium", sans-serif;
  font-size: 15rem;
  line-height: 165%;
  color: $title_color;
}

.top_single_bottom {
  display: none;
}

@media #{$mediaExtraLarge} and #{$media_1500} {
  .single_block {
    margin-left: 0;
  }

  .single_bottom .container {
    padding: 0;
  }

  .single_text {
    width: 100%;
    margin: 25rem 0;
  }

  .single_pretitle {
    margin-left: 0;
  }

  .article_info.single {
    margin-left: 0;
  }
}

@media #{$mediaLarge} {
  .single_block {
    margin-left: 0;
    width: 560rem;
  }

  .single_text {
    width: 100%;
    margin: 25rem 0;
  }

  .single_pretitle {
    margin-left: 0;
  }

  .article_info.single {
    margin-left: 0;
  }

  .single_bottom {
    margin-top: 50rem;
  }

  .single_bottom .container {
    padding: 0;
  }

  .topic_item {
    max-width: 18%;
  }
}

@media #{$mediaMedium} {
  .single_block {
    width: 100%;
  }

  .single .container {
    flex-direction: column;
  }

  .single .aside {
    margin-left: 0;
  }

  .single .aside {
    margin-top: 60rem;
  }

  .topic_item_link {
    font-size: 14rem;
  }

  div.top_single_bottom {
    display: block;
    padding: 0;
  }

  section.single_bottom {
    display: none;
  }

  .single_bottom_title {
    font-size: 21rem;
    margin-bottom: 35rem;
  }
}

@media #{$mediaSmall} {
  .single_bottom {
    padding: 0 30rem;
  }

  .topic_list {
    justify-content: space-around;
    flex-wrap: wrap;
  }

  .topic_item {
    max-width: 40%;
    width: 150rem;
    margin-bottom: 30rem;
  }
}

@media #{$mediaExtraSmall} {
  .single {
    padding-top: 85rem;
  }

  .single_title {
    font-size: 32rem;
    margin-bottom: 20rem;
    line-height: 32rem;
  }

  .single_text {
    font-size: 12rem;
    margin: 10rem 0;
  }

  .single_block .quotes {
    width: 100%;
    max-width: 100%;
    margin: 25rem 0 15rem;
  }

  .single_block .quotes .quotes_text {
    margin-left: 35rem;
  }

  .icon-left-quote {
    top: 0;
  }

  .single_pretitle {
    font-size: 21rem;
    margin: 25rem 67rem 10rem 0;
  }

  .article_info div {
    display: flex;
  }

  .article_info.single {
    margin: 0;
  }

  .article_info.single .author_name {
    font-size: 13rem;
  }
}



