@import "../mixins";
@import "../vars";

.portfolio_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.portfolio_page {
  width: 100%;
  display: flex;
  align-items: center;
  min-height: 100vh;
  padding: 0 60rem 0 60rem;
}

.slick-current .slider_nav_item {
  border: 1px solid $title_color;
}

.slider_nav_item {
  padding: 18rem;
  height: calc(100% / 5);

  img {
    width: 100%;
  }
}

.slider_nav_pic {
  height: 100%;
}

.slider_nav {
  margin-top: 60rem;
  height: 560rem;
  width: auto;
  max-width: 138rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;

  .slick-prev {
    color: $title_color;
    top: -25px;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 9;
    transform: rotate(-90deg);
    font-size: 13rem;
  }

  .slick-prev::before {
    content: '';
  }

  .slick-next {
    color: $title_color;
    bottom: -25px;
    left: 0;
    top: auto;
    right: 0;
    margin: auto;
    z-index: 9;
    transform: rotate(90deg);
    font-size: 13rem;
  }

  .slick-next::before {
    content: '';
  }

  .slick-slide {
    margin: 0 !important;
  }
}

.slider_list {
  height: 600rem;
  width: 1189rem;

  .slick-prev {
    width: auto;

    i::before {
      transform: rotate(-180deg);
      display: inline-block;
    }

    font-size: 14rem;
    color: $title_color;

    &::before {
      content: '';
    }
  }

  .slick-next {
    width: auto;
    color: $title_color;
    font-size: 14rem;

    &::before {
      content: '';
    }
  }
}

.slider_item {
  padding-left: 80rem;
  height: 100%;
  display: flex !important;
  position: relative;
}

.slider_item_right {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  img {
    height: 527rem;
  }
}

.slider_item_info {
  display: flex;
  width: 378rem;
  margin-top: 90rem;
  margin-right: 138rem;
  flex-direction: column;
}

.slider_category.article_category {
  color: #5d5d5d;
  font-size: 12rem;
}

.slider_text.article_text {
  max-height: 210rem;
  overflow-y: auto;
}

.slider_title.article_title {
  font-size: 50rem;
  line-height: 120%;
  font-weight: 700;
  letter-spacing: 0.05em;
  color: $title_color;
}

.slider_item_link {
  padding-bottom: 8rem;
  font-family: "SpaceGrotesk-Regular", sans-serif;
  font-size: 14rem;
  line-height: 13rem;
  margin-top: 33rem;
  margin-bottom: 3rem;
  color: $title_color;
  text-align: right;
  border-bottom: 1px solid $title_color;
  text-decoration: none;
}

.slider_item_list {
  position: absolute;
  width: 726rem;
  background-color: $dark_background;
  color: $text_color_light;
  height: 103rem;
  left: 0;
  bottom: 5rem;
  display: flex;
  justify-content: space-around;
  align-items: center;

  span {
    height: 40px;
    width: 1px;
    background-color: $text_color_light;
    display: block;
    color: transparent;
    opacity: 0.3;
  }
}

.slider_item_list_title {
  margin-bottom: 17rem;
  font-family: "SpaceGrotesk-Regular", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14rem;
  line-height: 13rem;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $text_color_light;
}

.slider_item_list_text {
  font-family: "SpaceGrotesk-Regular", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 12rem;
  line-height: 160%;
  color: $text_color_light;
}

@media (min-height: 900px) {
  .slider_nav {
    height: auto;
  }
}

@media #{$mediaExtraLarge} and #{$media_1500} {
  .portfolio_page {
    display: flex;
    align-items: center;
    padding: 0 60rem 0 60rem;
  }

  .portfolio_container.container {
    width: 1090rem;
  }

  .slider_nav {
    width: 12%;
    height: 480rem;
  }

  .slider_list {
    width: 80%;
    margin-left: 0;
  }

  .slider_item_list {
    width: 600rem;
    height: 85rem;
  }

  .slider_item_info {
    margin-right: 40rem;
    margin-top: 70rem;
  }

  .slider_title.article_title {
    font-size: 45rem;
    line-height: 110%;
    margin: 15px 0;
  }

  .article_text {
    font-size: 13rem;
  }

  .slider_item_list_title {
    margin-bottom: 5rem;
  }

  .slider_item {
    padding-left: 0;
  }

  .slider_nav_item {
    height: calc(480rem / 5);
  }
}

@media #{$mediaLarge} {
  .portfolio_page {
    display: flex;
    align-items: center;
    padding: 100rem 0 40rem;
  }

  .portfolio_container.container {
    width: 950rem;
  }

  .slider_nav {
    width: 12%;
    height: auto;
  }

  .slider_list {
    width: 80%;
    height: 480rem;
    margin-left: 0;
  }

  .slider_item {
    padding: 0;
  }

  .slider_item_list {
    width: 497rem;
    left: 0;
    height: 85rem;
  }

  .slider_item_info {
    margin-right: 40rem;
    margin-top: 20rem;
  }

  .slider_title.article_title {
    font-size: 40rem;
    line-height: 110%;
    margin: 15px 0;
  }

  .article_text {
    font-size: 13rem;
  }

  .slider_item_list_title {
    margin-bottom: 5rem;
  }

  .slider_item_right img {
    height: 390rem;
  }

  .slider_nav_pic {
    width: 100%;
  }

  .slider_nav_item {
    height: calc(430rem / 5);
  }

  .slider_text {
    font-size: 13rem;
  }

  .slider_nav_item {
    padding: 10rem;
  }
}

@media (max-height: 650px) {
  .portfolio_page {
    min-height: 100%;
    padding: 80rem 30rem 40rem;
  }
}

@media #{$mediaMedium} {
  .portfolio_page {
    min-height: 100%;
  }

  .portfolio_container.container {
    width: 750rem;
  }

  .slider_nav {
    display: none;
  }

  .slider_list {
    width: 100%;
    padding: 0 10rem;
  }

  .slider_item_list {
    width: 450rem;
  }

  .slider_list.slick-initialized .slick-slide {
    margin-right: 0 !important;
  }
}

@media #{$mediaSmall} {
  .portfolio_page {
    max-height: 100vh;
  }

  .portfolio_container.container {
    width: 100%;
    max-width: 550rem;
    padding: 0;
  }

  .slider_item_info {
    order: 3;
  }

  .slider_item_right {
    order: 1;
    align-items: flex-start;
    padding-top: 5rem;

    img {
      max-width: 200rem;
      height: auto;
    }
  }

  .slider_item_list {
    order: 2;
    flex-direction: column;
    align-items: flex-end;
    padding: 20rem 40rem;
    width: 80%;
    top: 5rem;
    height: 180rem;
    right: 0;
    z-index: -1;
  }

  .slider_item {
    flex-wrap: wrap;
    padding-bottom: 45rem;
  }

  .slider_title.article_title {
    font-size: 35rem;
  }

  .article_text {
    height: 150rem;
    overflow-y: scroll;
  }

  .slider_item_info {
    width: 100%;
    z-index: 99;
    margin-right: 0;
  }

  .slider_item_right .slider_item_link {
    position: absolute;
    bottom: 7rem;
    padding-bottom: 4rem;
    left: 0;
  }

  .slider_category.article_category {
    display: none;
  }

  .slider_item_list_title {
    font-size: 10rem;
  }

  .slider_item_list_text {
    font-size: 10rem;
  }

  .slider_item_list span {
    width: 70rem;
    height: 1rem;
  }

  .slider_list .slick-next {
    right: 115rem;
    z-index: 1;

    i {
      font-size: 10rem;
      margin-left: 5rem;
    }
  }

  .slider_list .slick-prev {
    right: 180rem;
    z-index: 1;
    left: auto;

    i {
      font-size: 10rem;
      margin-right: 5rem;
    }
  }

  .slick-prev::before,
  .slick-next::before {
    display: none;
  }
}

@media #{$mediaExtraSmall} {
  .portfolio_page {
    padding: 80rem 10rem 40rem;
  }

  .slider_item_right {
    width: 100%;

    img {
      max-width: 200rem;
      width: 55%;
    }
  }

  .slider_list {
    max-width: 430rem;
    margin: 0 auto;
  }

  .slider_item_list {
    width: 100%;
  }

  .slider_list .slick-next {
    right: 10rem;
    font-size: 12rem;
    top: 200rem;

    i {
      font-size: 8rem;
      margin-left: 5rem;
    }
  }

  .slider_list .slick-prev {
    right: 70rem;
    font-size: 12rem;
    top: 200rem;

    i {
      font-size: 8rem;
      margin-right: 5rem;
    }
  }

  .slider_title.article_title {
    font-size: 32rem;
  }

  .article_text {
    font-size: 12rem;
  }

  .slider_item_right .slider_item_link {
    bottom: 45rem;
  }

  .slider_text.article_text {
    padding-bottom: 30rem;
  }
}



